nav ul {
    list-style-type: none;
    display: flex;
    justify-content: space-around;
    padding: 0;
    background-color: #333;
  }
  
  nav li {
    margin: 10px;
  }
  
  nav a {
    color: white;
    text-decoration: none;
    padding: 10px;
  }
  
  nav button {
    background: none;
    border: none;
    color: white;
    cursor: pointer;
    padding: 10px;
  }
  
  nav ul {
    display: flex;
    flex-wrap: wrap;
  }
  
  @media (max-width: 600px) {
    nav ul {
      flex-direction: column;
      align-items: center;
    }
  }
  